const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			imgUrl: '',
			Inv: '0',
			tabType: 'board'
		};
	},
	onLoad() {
		// 加载关注列表
		this.getList()
		this.imgUrl = helper.imgUrl;
	},
	methods: {
		changeTab(e) {
			console.log(e);
			this.Inv = e;
			this.page = '1';
			if (this.Inv == 0) {
				// 最新
				this.tabType = 'board';
			} else if (this.Inv == 1) {
				// 热门
				this.tabType = 'city';
			} else if (this.Inv == 2) {
				// 推荐
				this.tabType = 'user';
			}
			this.list = [];
			this.getList();
		},
		// 跳转详情
		getDetail(id,title,img,type){
			if(type == '城市'){
				uni.navigateTo({
					url:`/pages/exchangList?title=${title}&id=${id}&img=${img}`
				})
			}else{
				uni.navigateTo({
					url:`/pages/boardData?title=${title}&id=${id}&img=${img}`
				})
			}
			
		},
		getList() {
			// 加载关注列表
			uni.request({
				url: `${helper.requestUrl}/user/follows`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					type: this.tabType
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.list = res.data.data;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		delFollows(e) {
			let id = e.currentTarget.dataset.id;
			let index = e.currentTarget.dataset.index;
			uni.request({
				url: `${helper.requestUrl}/user/del_follow`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					id: id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.list.splice(index, 1);
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
